import ConnectButton from "@/components/connect-button";
import Web3ContextProvider from "@/context/web3Context";




export default function Layout({
    children,
}: Readonly<{
    children: React.ReactNode;
}>) {
    return (

        <div>
            <Web3ContextProvider>
                <header className="">
                    <div id="nav" className=" flex justify-between px-16 py-4 mb-4 font-medium ">
                        <div className=" flex gap-32  items-center">
                            <div>
                                Logo
                            </div>
                            <div >
                                <div className=" flex gap-4">
                                    <div className=" rounded-2xl px-4 py-1 bg-white/10 hover:backdrop-blur-3xl">
                                    </div>
                                    <div className=" rounded-2xl px-4 py-1 hover:bg-white/10 hover:backdrop-blur-3xl">Mint</div>
                                    <div className=" rounded-2xl px-4 py-1 hover:bg-white/10 hover:backdrop-blur-3xl">Your Assets</div>
                                    <div className=" rounded-2xl px-4 py-1 hover:bg-white/10 hover:backdrop-blur-3xl">Transactions</div>
                                </div>
                            </div>
                        </div>
                        <div className=" flex gap-4 items-center">
                            <div className=" rounded-2xl px-4 py-1 hover:bg-white/10 hover:backdrop-blur-3xl">X</div>
                            <div className=" rounded-2xl px-4 py-1 hover:bg-white/10 hover:backdrop-blur-3xl">D</div>
                            <ConnectButton />
                        </div>
                    </div>
                </header>
                {children}

            </Web3ContextProvider>
        </div>
    );
}